import './App.css';
import Navbar from './components/Navbar';
import Righthhand from './components/Righthhand';
import Lefthand from './components/Lefthand';
import Mid from './components/Mid';
import { useState} from 'react'


function App() {

  // UseStates
  const [darkMode, changeDarkMode] = useState(true)

  // Function Which will be called As Global
  const setdarkMode = () => {
    if (darkMode) changeDarkMode(false)
    else changeDarkMode(true)
  }

  // const getdata = async () => {
  //   await db.collection('cards').get().then((snapshot) => {
  //     const data = snapshot.docs.map(doc => doc.data());
  //     console.log(data);
  //   })
  // }


  // useEffect(() => {
  //   getdata()
  // }, [])

  // render()
  return (
    <>
      <Navbar SetDarkMode={setdarkMode} DarkMode={darkMode} />
      <div className="maincontainer">
        <Lefthand DarkMode={darkMode} />
        <Mid DarkMode={darkMode} />
        <Righthhand DarkMode={darkMode} />
      </div>
    </>
  );
}

export default App;
